@import "../../assests/style/app";

.app {

  .main {
    position: relative;
    min-height: calc(100vh - 60px);
    box-sizing: border-box;
    padding: 8px;

    @include desktop {
      padding: 12px;
    }

    .preloader {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: 888;

      span {
        height: 10px;
      }
    }

  }
}
