@import "../../assests/style/app";

.statistic {
  position: relative;

  .top {
    background: transparent;
    outline: 8px solid rgba(0,0,0,0);

    &_back {
      background: rgba(0,0,0,0.4);
      outline: 8px solid rgba(0,0,0,0.4);
      transition: 0.3s;

      @include desktop {
        background: none;
        outline: none;
      }
    }

    position: relative;
    z-index: 777;

    .panelWrapper {
      position: relative;
      z-index: 777;
      margin-top: 8px;

      @include desktop() {
        margin-top: 0;
      }
    }
  }


}
