@import "../../assests/style/app";

.header {
  position: relative;
  z-index: 999;
  height: 60px;
  background: #263238;
  padding: 0 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include desktop {
    padding: 0 32px;
  }

  .logo {
    text-decoration: none;
    display: flex;
    align-items: center;
    position: relative;

    .icon {
      @include centerColumn;
      width: 42px;
      height: 42px;

      svg {
        width: 42px;
        height: 42px;
        transform-origin: center;

        path {
          transition: 0.3s;
        }

      }
    }

    p {
      color: #FFF;
      margin-left: 8px;
      font-size: 24px;
      transition: 0.3s;
    }

    &:hover {
      svg path {
        fill: orange;
      }

      p {
        color: orange;
      }
    }
  }

  .links {
    @include onlyDesktop(flex);
    @include absoluteCenter;

    .link {
      text-decoration: none;
      color: #FFF;
      font-size: 18px;
      transition: 0.3s;

      &_active {
        color: orange;
      }

      &:not(:first-child) {
        margin-left: 24px;
      }

      &:hover {
        color: orange;
      }

    }

  }

  .right {
    display: flex;
    align-items: center;

    .burgerButton {
      @include onlyMobile(flex);
      margin-left: 16px;

      svg path {
        fill: #FFF;
      }
    }
  }


}
