@import "../../../assests/style/app";

.card {
  border: 1px solid #263238;
  border-radius: 12px;
  padding: 4px;

  .title {
    background: #263238;
    color: #FFF;
    padding: 8px 0;
    text-align: center;
    border-radius: 8px;
    font-size: 18px;
    font-weight: bold;
  }

  .selectWrapper {
    height: 40px;
    margin-top: 8px;
  }

  .values {
    margin-top: 8px;
    padding: 0 8px 4px;

    .row {
      display: grid;
      grid-template-columns: repeat(2, 1fr);

      align-items: center;

      &:not(:first-child) {
        margin-top: 4px;
      }

      .label {
        font-weight: bold;
      }

      .valueWrapper {
        display: flex;
        align-items: center;

        p {
          margin-left: 8px;
        }
      }


    }

  }

}

