@import "../../../assests/style/app";

.showPanelButton {
  @include onlyMobile(flex);
  @include button;
  @include centerRow;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(35, 50, 56, 0.75);
  position: relative;
  z-index: 999;

  svg {
    transform: rotate(-180deg);
    transition: 0.3s;
  }

  &_show {
    svg {
      transform: rotate(0deg);
    }
  }
}
