@import "../../assests/style/app";

.burgerMenu {
  @include centerColumn;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding-top: 60px;
  box-sizing: border-box;
  z-index: 888;
  background: grey;
  transform: translateX(101%);
  transition: 0.3s;

  &_show {
    transform: translateX(0%);
  }

  .links {

    .link {
      display: block;
      text-align: center;
      text-decoration: none;

      color: #FFF;
      font-size: 24px;
      transition: 0.3s;

      &:not(:first-child) {
        margin-top: 36px;
      }

      &_active {
        color: lime;
      }
    }
  }

}
