@import "../../../assests/style/app";

.selectorsPanel {
   position: relative;

  .top {
    display: flex;
    flex-direction: column;

    @include desktop {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-column-gap: 16px;
    }

    .row {
      display: flex;

      @include desktop {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 4px;
      }

      .label {
        @include centerRow;
        padding: 0 16px;
        height: 40px;
        background: #263238;
        color: #FFF;
        border-radius: 2px;

        @include desktop {
          justify-content: flex-end;
        }
      }

      .select {
        margin-left: 4px;

        @include desktop {
          margin-left: 0;
        }
      }

      &:not(:first-child) {
        margin-top: 4px;

        @include desktop {
          margin-top: 0;
        }
      }
    }

  }

  .bottom {
    margin-top: 4px;

    @include desktop {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-column-gap: 16px;
    }


    .periodWrapper {

      @include desktop {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 4px;
      }

      .radio {
        flex-wrap: nowrap;

        @include desktop {
          justify-content: flex-end;
        }
      }

      .inputWrapper {
        margin-top: 4px;

        @include desktop {
          margin-top: 0;
        }

        .dateWrapper {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          grid-column-gap: 4px;
        }

      }

    }

    .valueChangeTypeWrapper {
      margin-top: 4px;

      @include desktop {
        margin-top: 0;
      }
    }
  }

}
