.customTooltip {
  padding: 8px;
  background: #FFF;
  border: 1px solid #263238;
  border-radius: 5px;

  .label {
    color: #000;
    font-weight: bold;
    text-decoration: underline;
  }

  .value {
    margin-top: 4px;

    span:first-child {
      color: grey;
    }

    span:last-child {
      font-weight: bold;
    }
  }
}
